<script>
import PaginationTemplate from '@/components/PaginationTemplate.vue'
import Offcanvas from 'bootstrap/js/dist/offcanvas.js'
import Modal from 'bootstrap/js/dist/modal.js'
import Dropdown from 'bootstrap/js/dist/dropdown.js'
export default {
    name: "Main",
    data(){
        return{
            card_modal: null,
            display_card: null,
            display_page: "active",
            pack_data: null,
            filter_data: null,
            filter_: {
                attr: new Array(),
                race: new Array(),
                star: new Array(),
            },
            sort: "attr",
            page: 1,
            pageSize: 120,
            opt1:{
                chunk:3,
                template: PaginationTemplate,
            },
            opt2:{
                chunk:5,
                template: PaginationTemplate,
            },
            opt3:{
                chunk:10,
                template: PaginationTemplate,
            },
        }
    },
    created(){
        this.pack_data = this.monster_data
        this.pack_data.sort(function(a, b) {
            if(b.attribute == a.attribute){
                return a.id - b.id;
            }
            return a.attribute - b.attribute
        });
        this.filter_data = this.pack_data.filter(() => {
            return true;
        })
    },
    mounted(){
        [].slice.call(document.querySelectorAll('.offcanvas')).map(function (offcanvasEl) {
            return new Offcanvas(offcanvasEl)
        })
        new Array().slice.call(document.querySelectorAll('.dropdown-toggle')).map(function (dropdownToggleEl) {
            return new Dropdown(dropdownToggleEl)
        })
        this.card_modal = new Modal(this.$refs.card_modal)
        document.title = "圖鑑"
    },
    computed:{
        filter_count(){
            return this.filter_.attr.length + this.filter_.race.length + this.filter_.star.length + this.filter_.level.length
        },
        indexStart() {
            return (this.page - 1) * this.pageSize;
        },
        indexEnd() {
            return this.indexStart + this.pageSize;
        },
        paginated() {
            return this.filter_data.slice(this.indexStart, this.indexEnd);
        },
        monster_data(){
            return this.$store.state.monster_data
        },
        monster_active(){
            return this.$store.state.monster_active
        },
        monster_leader(){
            return this.$store.state.monster_leader
        },
        monster_team(){
            return this.$store.state.monster_team
        }
    },
    methods:{
        avatar_src(id){
            if(parseInt(id) < 10){
                id = "000" + id
            }else if(parseInt(id) < 100){
                id = "00" + id
            }else if(parseInt(id) < 1000){
                id = "0" + id
            }
            return "https://web-assets.tosconfig.com/gallery/icons/" + id + ".jpg"
        },
        filter(){
            this.filter_data = this.pack_data.filter((item) => {
                let tmp1 = 0, tmp2 = 0, tmp3 = 0
                if(this.filter_.attr.length == 0){
                    tmp1 = 1
                }else if(this.filter_.attr.indexOf(item.attribute.toString()) != -1){
                    tmp1 = 1
                }
                if(this.filter_.race.length == 0){
                    tmp2 = 1
                }else if(this.filter_.race.indexOf(item.race.toString()) != -1){
                    tmp2 = 1
                }
                if(this.filter_.star.length == 0){
                    tmp3 = 1
                }else{
                    if(this.filter_.star.indexOf(item.star.toString()) != -1){
                        tmp3 = 1
                    }else if(this.filter_.star.indexOf("5d") != -1 && item.star < 5){
                        tmp3 = 1
                    }
                }
                return tmp1 && tmp2 && tmp3
            })
        },
        display_leader(id){
            let leader = this.monster_leader.find((item) => {
                return item[0] == id
            })
            if(leader == undefined){
                return {description: "", name: ""}
            }
            leader = leader[1]
            leader.name = leader.name.replace(/['"]+/g, '')
            leader.description = leader.description.replace(/['"]+/g, '')
            return leader
        },
        display_active(id){
            let active = this.monster_active.find((item) => {
                return item[0] == id
            })
            if(active == undefined){
                return {description: "", name: ""}
            }
            active = active[1]
            active.name = active.name.replace(/['"]+/g, '')
            active.description = active.description.replace(/['"]+/g, '')
            return active
        },
        display_team(id){
            let team = this.monster_team.find((item) => {
                return item[0] == id
            })
            if(team == undefined){
                return ""
            }
            return team[1].replace(/['"]+/g, '')
        },
        sort_attr(){
            this.filter_data.sort(function(a, b) {
                if(b.attribute == a.attribute){
                    return a.id - b.id;
                }
                return a.attribute - b.attribute
            });
        },
        sort_id(){
            this.filter_data.sort(function(a, b) {
                return a.id-b.id
            });
        },
    },
    watch:{
        'filter_.attr'(){
            this.page = 1
            this.filter();
        },
        'filter_.race'(){
            this.page = 1
            this.filter();
        },
        'filter_.star'(){
            this.page = 1
            this.filter();
        },
        'filter_.level'(){
            this.page = 1
            this.filter();
        },
        sort(){
            if(this.sort == "attr"){
                this.sort_attr()
            }
            if(this.sort == "id"){
                this.sort_id()
            }
        }
    }
}
</script>

<style scoped>
    /* HIDE RADIO */
    [type=checkbox] { 
        position: absolute;
        opacity: 0;
        width: 0;
        height: 0;
    }

    /* IMAGE STYLES */
    [type=checkbox] + img {
        cursor: pointer;
        filter: brightness(.4);
    }

    /* CHECKED STYLES */
    [type=checkbox]:checked + img {
        filter: brightness(1);
    }
</style>